
@use '../../../themes/theme1/vars';
@use '../../../themes/theme1/main' as Tmain;
@use '../../../general/main' as Gmain;
@use '../../../general/mediaQueries';
@use '../../../general/buttons' as Gbuttons;
@use '../../../themes/theme1/buttons' as Tbuttons;
@use '../../../PUBLIC/profile/subPages/Adresses';
@use '../../../../../../Splint/scss/tools/tools' as S_tools;

@mixin checkout_Overview(){
    > .CheckOrderMain {
        @include mediaQueries.forMediaBreakpoint(mobile-small){
            font-size: 2em;
        }
        > div {
            margin-bottom: 0.5em;
            @include mediaQueries.forMediaBreakpoint(mobile-small){
                margin-bottom: 1em;
            }
            padding: 0.5rem;
            border: none;
            &:not(.buttonsDiv){
                // &:not(.paypalContainer){
            @include Tmain.TContainer_basic();
                // }
            }
            &.buttonsDiv{
                >button {
                    @include mediaQueries.forMediaBreakpoint(mobile-small){
                        width: 100%;
                    }
                }
            }
        }
        > .paypalContainer {
            // width: 100%;
            position: relative;
            // padding: auto;
            display: flex;
            justify-content: center;
            align-items: center;
            > div {
                min-width: 80%!important;
                max-width: 100%!important;
                position: relative;
            }
        }
        > .CreditCardContainer {
            > .content {
                > .PPcontainer {
                    position: relative;
                    width: 100%;
                    height: 4rem;
                    max-height: 4rem;
                    overflow: hidden;
                    background-color: transparent;
                    left: 0;
                    top: 0;
                    // > div {
                    //     position: relative;
                    //     height: 100% !important;
                    //     * {
                    //         position: relative;
                    //         height: 100% !important;
                    //     }
                    // }
                }
                > button {
                    display: none;
                    // @include Tmain.TButton_simple();
                }
            }
        }
    }
}
